import React from "react";
import VideoTree from "./VideoTree/VideoTree";
import { generateRandomInt } from "../../../utils/utils.js";
import BreadCrumbs from "../../../Components/Material/BreadCrumbs";
import Ajax from "../../../utils/ajax";
import config from "../../../utils/config";
import { Card } from "../../../Components/Material/Card";
import Styles from "../../../Components/Material/Styles.module.css";
import { AppBar } from "../../../Components/Material/AppBar";
import { SideBar } from "../../../Components/Material/SideBar";
import LoadingOverlay from "../../../Components/LoadingOverlay";

import "../style.css";
import { theme } from "../../../Components/Material/Variables.js";

class Edit extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoading: true,
      project: {
        id: 0,
        title: "",
        settings: {}
      },
      nodes: [],
      reloadPlayer: 0 // just increment to trigger
    };

    this.createNode = this.createNode.bind(this);
    this.updateNode = this.updateNode.bind(this);
    this.deleteNode = this.deleteNode.bind(this);
    this.editProjectTitle = this.editProjectTitle.bind(this);
    this.reloadPlayer = this.reloadPlayer.bind(this);
  }

  reloadPlayer() {
    this.setState(prevState => {
      return { reloadPlayer: prevState.reloadPlayer + 1 };
    });
  }

  createNode(parentId) {
    let node = {
      id: generateRandomInt(10000000, 90000000), // temp id, server will generate new one
      parent_id: parentId,
      title: "New Node",
      url: ""
    };

    Ajax.post(`/projects/${this.props.match.params.id}/nodes`, node)
      .then(res => {
        if (typeof res.node !== "undefined") {
          // let nodes = [...this.state.nodes];
          // console.log("createnode: ", nodes);

          // nodes.push(res.node);
          const nodes = this.state.nodes.map(n => {
            if (n.id === node.id) {
              // replace temp id with server id
              n = res.node;
              console.log("n: ", n);
            }
            return n;
          });
          console.log("createnode: ", nodes);
          this.setState({ nodes });
          this.reloadPlayer();
        }
      })
      .catch(err => console.error(err));

    let nodes = this.state.nodes.map(n => JSON.parse(JSON.stringify(n)));
    nodes.push(node);
    this.setState({ nodes });
  }

  updateNode(node) {
    let nodes = [...this.state.nodes];

    for (let i = 0; i < nodes.length; i++) {
      if (node.id === nodes[i].id) {
        nodes[i] = node;
        Ajax.put(
          `/projects/${this.props.match.params.id}/nodes/${node.id}`,
          node
        )
          .then(res => {
            this.setState({ nodes });
            this.reloadPlayer();
          })
          .catch(err => console.error(err));

        return;
      }
    }
  }

  deleteNode(id) {
    Ajax.delete(`/projects/${this.props.match.params.id}/nodes/${id}`)
      .then(res => this.reloadPlayer())
      .catch(err => console.error(err));
    let nodes = this.state.nodes.filter(n => n.id !== id);
    this.setState({ nodes });
  }

  editProjectTitle(newTitle) {
    let project = { ...this.state.project };
    project.title = newTitle;

    Ajax.put(this.props.match.url, project).catch(err => console.error(err));

    this.setState({ project });
    // TODO save new title to server
  }

  componentDidMount() {
    let url = `/projects/${this.props.match.params.id}`;

    Ajax.get(url, [])
      .then(res => {
        if (typeof res.project.id !== "undefined") {
          let state = { ...this.state };
          state.project = res.project;
          if (typeof res.project.nodes !== "undefined") {
            state.nodes = res.project.nodes;
            state.isLoading = false;
          }
          this.setState(state);
        }
      })
      .catch(err => console.error(err));
  }

  render() {
    const links = [
      { name: "projects", path: "/projects" },
      {
        name: this.state.project.title,
        path: `/projects/${this.props.match.params.id}`,
        edit: this.editProjectTitle
      }
    ];
    return (
      <div className="grid-wrapper">
        <div className="topnav-area">
          <AppBar />
        </div>
        <div className="sidebar-area">
          <SideBar>
            <h3>Player settings</h3>
            <label>Autoplay</label>
            <input type="checkbox" />
          </SideBar>
        </div>
        <div className="breadcrumbs-area">
          <BreadCrumbs links={links} />
          <div style={{ clear: "both", marginBottom: "30px" }} />
        </div>
        <div
          className="content-area"
          style={{ backgroundColor: theme.palette.background.level2 }}
        >
          {this.state.isLoading ? (
            <LoadingOverlay />
          ) : (
            <>
              {this.state.nodes.length < 1 ? (
                ""
              ) : (
                <VideoTree
                  projectId={this.state.project.id}
                  nodes={this.state.nodes}
                  nodesKey="nodes"
                  createNode={this.createNode}
                  updateNode={this.updateNode}
                  deleteNode={this.deleteNode}
                />
              )}
              <Card
                position="absolute"
                right="15"
                bottom="15"
                width="300"
                height="300"
                className={Styles.elevation1}
              >
                {this.state.project.id !== 0 ? (
                  <div className="player-preview-popup-iframe-wrapper">
                    <iframe
                      title="Player Preview"
                      key={this.state.reloadPlayer}
                      // TODO check autoplay param in player and disable autoplay
                      src={`${config.playerUrl}?projectId=${
                        this.state.project.id
                      }&autoplay=false`}
                      allow="autoplay"
                    />
                  </div>
                ) : (
                  "No project"
                )}
              </Card>
            </>
          )}
        </div>
      </div>
    );
  }
}

export default Edit;
